import React from "react";
import SlideshowNoel from "./components/Carousel";
import "./components/Carousel.css";
import "./styles.css";
import productsNoel from "./components/products/productsNoel";



function App() {
  return (
    <div className="App">
      
      {productsNoel.map(product => (
        <SlideshowNoel
          title={product.title}
          price={product.price}
          description={product.description}
          link={product.link}
          reference={product.reference}
          images={product.images}
        />
      ))}
    </div>
  );
}

export default App;
